
import React, { useEffect, useState } from "react";
import "./UserRankings.css";
import { Link } from "react-router-dom";

const UserRankings = () => {
  const [rankings, setRankings] = useState([]);

  useEffect(() => {
    fetch("http://localhost:5500/api/userrankings")
      .then(res => res.json())
      .then(data => setRankings(data))
      .catch(err => console.error("Error fetching user rankings:", err));
  }, []);

  return (
    <div className="user-rankings">
      <h2 className="main-title">User Rankings</h2>
      <p className="subtitle">See where you stand among your peers!</p>

      {rankings.map((section, index) => (
        <div className="ranking-section" key={index}>
          <div className="ranking-image">
            <img src={section.image} alt={section.sectionTitle} loading="lazy" />
          </div>

          <div className="ranking-content">
            <h3 className="ranking-title">{section.sectionTitle}</h3>
            <p className="ranking-desc">{section.sectionDescription}</p>
            {section.linkText && section.linkPath && (
              <Link to={section.linkPath} className="ranking-btn">
                {section.linkText}
              </Link>
            )}
          </div>
        </div>
      ))}
    </div>
  );
};

export default UserRankings;
